<script>
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import CardModule from '@/components/common/kanban/CardModule';
import { fontSize } from '@/utils/common';
// import distributes from '@/views/kanban_bsb/distributes.vue';
import distributes from '@/views/kanban_bsb/distributes2.vue';
// 导入所需接口
